/*--------------------選單開始------------------*/
.navbar {
	transition: all 0.2s ease;
	align-items: flex-start;
	background: $secondary-color; /* Old browsers */
	background: -moz-linear-gradient(left, $secondary-color 0%, $secondary-light-color 100%); /* FF3.6-15 */
	background: -webkit-linear-gradient(
		left,
		$secondary-color 0%,
		$secondary-light-color 100%
	); /* Chrome10-25,Safari5.1-6 */
	background: linear-gradient(
		to right,
		$secondary-color 0%,
		$secondary-light-color 100%
	); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */

	&-brand {
		height: 35px;
		padding: 0;
		background: url("../images/logo-header.png") no-repeat;
		background-size: contain;
		padding-left: 265px;
		display: flex;
		align-items: center;
		@include PC-sm {
			height: 30px;
			background: url("../images/logo-header-mobile.png") no-repeat left center;
			background-size: 25px;
			padding-left: 30px;
		}
	}
	&-title {
		text-shadow: 0 0 3px $gray-dark;
		@include mobile-lg {
			font-size: $font-xxxs;
		}
		@include mobile-md {
			display: none;
		}
	}
	&-toggler {
		display: flex;
		align-items: center;
		color: $secondary-dark-color;
		border-radius: 50rem;
		font-size: $font-sm;
		padding: 2px 12px !important;
		background: rgba(255, 255, 255, 0.6);
		&:hover {
			background: rgba(255, 255, 255, 1);
		}
		&:focus {
			box-shadow: none;
		}
	}
	&-collapse {
		@include iPad {
			padding: 10px 0;
		}
	}
	.btn-input-group {
		@include PC {
			width: 200px;
		}
		@include iPad {
			width: 100%;
			max-width: 300px;
			margin: 0 auto;
		}
	}
}
.nav-item {
	@include iPad {
		display: flex;
		justify-content: center;
		border-bottom: 1px solid $secondary-light-color;
		&:nth-child(2) {
			.nav-link {
				border-radius: 8px 8px 0 0;
			}
		}
		&:last-child {
			.nav-link {
				border-radius: 0 0 8px 8px;
			}
		}
	}
}
.nav-link {
	padding: 4px 8px;
	@include iPad {
		display: flex;
		color: $white;
		padding: 10px 0;
		width: 100%;
		background: $secondary-dark-color;
		justify-content: center;
		img {
			filter: invert(100%) sepia(0%) saturate(1%) hue-rotate(88deg) brightness(201%) contrast(200%);
			margin-right: 10px;
			width: 24px;
		}
	}
	&:hover {
		opacity: 0.8;
	}
}

/*------------------------footer開始----------------------------*/
.gotop {
	display: block;
	width: 57px;
	height: 37px;
	display: flex;
	justify-content: center;
	align-items: center;
	position: fixed;
	right: 10px;
	bottom: 10px;
	padding: 0;
	background: url(../images/gotop.svg);
}
footer {
	.footer-logo {
		max-width: 100px;
		@include iPad {
			max-width: 60px;
		}
	}
}

/*------------------------測邊選單開始----------------------------*/
.side-menu {
	margin-top: 54px;
	padding-top: 30px;
	width: 350px;
	height: calc(100vh - 54px);
	position: fixed;
	left: 0;
	transition: all 0.2s ease;
	z-index: 10;
	background: $white;
	box-shadow: 0 0 10px $gray;
	@include PC {
		width: 280px;
		padding-top: 16px;
	}
	@include iPad {
		margin-top: 46px;
		min-height: calc(100vh - 46px);
	}
	&.close {
		left: -350px;
		@include PC {
			left: -280px;
		}
		.side-menu-toggler {
			img {
				transform: rotate(180deg);
			}
		}
	}
}
.side-menu-body {
	height: calc(100% - 50px);
	overflow: auto;
}
.accordion-collapse {
	padding: 0 15px;
	@include PC {
		padding: 0 8px;
	}
}
.accordion-header {
	padding: 10px 15px 0 15px;
	font-size: $font-sm;
	@include PC {
		padding: 10px 8px 0 8px;
	}
	@include iPad {
		padding: 6px 8px 0 8px;
	}
}
.accordion-button {
	font-size: $font-smd;
	padding: 10px 16px 10px 24px;
	color: $tertiary-color;
	margin-bottom: 10px;
	@include PC {
		padding: 8px 8px 8px 16px;
		font-size: $font-sm;
		margin-bottom: 8px;
	}
	@include iPad {
		margin-bottom: 6px;
	}
	&::after {
		width: 24px;
		background: url("../images/ic-arrow.svg");
		background-size: contain;
	}
	&:not(.collapsed) {
		font-weight: $font-bold;
		color: $primary-color;
		background: $primary-xlight-color;
		border-left: 7px solid $primary-color;
		box-shadow: none;
		border-radius: 0;
		padding: 10px 20px 10px 15px;
		margin-bottom: 0;
		@include PC {
			border-left: 5px solid $primary-color;
			padding: 8px 12px 8px 12px;
		}
		&::after {
			width: 24px;
			background: url("../images/ic-arrow-active.svg");
			background-size: contain;
		}
		img {
			filter: invert(48%) sepia(65%) saturate(526%) hue-rotate(166deg) brightness(103%) contrast(94%);
		}
	}
}
.accordion-sub {
	background: none;
	font-size: $font-sm;
	padding: 4px 16px 4px 30px;
	&:after {
		position: absolute;
		height: 24px;
		left: 0;
		top: 6px;
		background: url("../images/ic-plus.svg");
	}
	&:not(.collapsed) {
		background: none !important;
		border: none !important;
		padding: 4px 16px 4px 30px !important;
		&:after {
			background: url("../images/ic-minus-pr.svg") !important;
		}
	}
}
.accordion-subsub {
	background: none;
	padding: 4px 16px 4px 40px;
	font-size: $font-sm;
	margin-bottom: 0;
	&:after {
		position: absolute;
		height: 24px;
		left: 12px;
		top: 6px;
		background: url("../images/ic-plus-sub.svg");
	}
	&:not(.collapsed) {
		background: none !important;
		border: none !important;
		padding: 4px 16px 4px 40px !important;
		color: $secondary-xdark-color;
		&:after {
			background: url("../images/ic-minus-sub.svg") !important;
		}
	}
	& ~ .accordion-collapse {
		padding-left: 40px;
	}
}
.accordion-item {
	border: none;
	border-bottom: 1px solid $gray-xlight;
}
.accordion-body {
	background: $primary-xxlight-color;
	margin-bottom: 10px;
	border-radius: 0 0 5px 5px;
	padding: 15px 10px;
	.input-group {
		width: calc(100% - 40px) !important;
	}
}
.accordion-sub-body {
	font-size: $font-xs;
	position: relative;
	padding: 10px 15px 0 40px;
	&:after {
		content: "";
		display: block;
		height: 100%;
		width: 1px;
		background: $primary-light-color;
		position: absolute;
		left: 23px;
		top: 0;
	}
}
.copyright {
	position: absolute;
	left: 0;
	bottom: 20px;
	font-size: $font-xxs;
	background: $white;
	padding: 10px 0;
	@include mobile-md {
		font-size: $font-xxxs;
		bottom: 10px;
	}
}
.side-menu-link {
	display: block;
	background: $pale-light-color;
	color: $gray-dark;
	padding-top: 10px;
	padding-bottom: 10px;
	&:hover {
		background: $white !important;
		color: $tertiary-color;
	}
}
.side-menu-toggler {
	width: 26px;
	height: 54px;
	border-radius: 0 8px 8px 0;
	position: absolute;
	right: -26px;
	top: 30px;
	justify-content: center;
	align-items: center;
	cursor: pointer;
	z-index: 10;
	&.close {
		img {
			transform: rotate(180deg);
		}
	}
}
.side-menu-item {
	width: 100%;
	&:hover {
		color: $primary-color;
	}
}
.side-menu-sub {
	display: flex;
	width: 100%;
	color: $primary-color;
	padding: 0 10px;
	&.collapsed {
		color: $gray-dark;
		.side-menu-icon {
			opacity: 0.8;
		}
	}
	&:focus {
		box-shadow: none;
	}
}
.side-menu-icon {
	background: $primary-color;
	border-radius: 50%;
	width: 26px;
	height: 26px;
	display: flex;
	justify-content: center;
	align-items: center;
	padding: 0;
	opacity: 1;
	margin-right: 15px;
	img {
		width: 21px;
	}
}
/*------------------------內容區塊開始----------------------------*/
.side-content {
	width: calc(100% - 350px);
	height: calc(100vh - 54px);
	margin-left: 350px;
	transition: all 0.2s ease;
	margin-top: 54px;
	overflow: hidden;
	position: relative;
	display: flex;
	flex-direction: column;
	@include PC {
		width: calc(100% - 280px);
		margin-left: 280px;
	}
	@include iPad {
		margin-top: 46px;
		width: 100%;
		margin-left: 0;
		height: calc(100vh - 46px);
	}
	&.close {
		width: 100%;
		margin-left: 0;
	}
}
.map-content {
	height: calc(100vh - 54px);
	overflow: hidden;
	&.open {
		height: calc(100vh - 314px);
	}
	@include iPad {
		height: calc(100vh - 46px);
	}
	iframe {
		height: 100%;
	}
	&.ui-state-disabled {
		opacity: 1;
	}
}
.side-info {
	height: 260px;
	width: 100%;
	flex: 1;
	overflow: auto;
	display: none;
	position: relative;
	&.open {
		display: block;
	}
	&.hide {
		pointer-events: none;
	}
	.input-group {
		max-width: 350px;
	}
	&-closer {
		position: absolute;
		right: 16px;
		top: 7px;
	}
	iframe {
		height: calc(100% - 10px);
	}
}
.side-info-header {
	padding: 7px 24px;
	display: flex;
	position: fixed;
	left: 0;
	top: 0;
	width: 100%;
	z-index: 100;
	@include mobile-md {
		padding: 7px 10px;
	}
	img {
		filter: brightness(150);
	}
}
.side-info-body {
	margin-top: 40px;
	padding: 15px 24px;
	position: relative;
	@include mobile-md {
		padding: 10px;
	}
	.circle-btn {
		flex: 0 0;
		flex-basis: 30px;
	}
}
.side-info-button{
	button{
		position: relative;
	z-index: 10;
	}
}
.status {
	border-radius: 4px 30px 30px 4px;
	display: flex;
	align-items: center;
	img {
		width: 18px;
	}
	&-txt {
		font-size: $font-md;
		@include PC {
			font-size: $font-sm;
		}
	}
}
.btn-sm {
	img {
		width: 15px;
	}
}
hr {
	margin: 25px 0;
}
/*------------------------表格開始----------------------------*/
.rwd-table {
	overflow: hidden;
	min-width: 100%;
	tr:nth-of-type(2n) {
		background: darken($color: $gray, $amount: 5%);
	}
	tr {
		@include mobile-lg {
			background: #012d46;
		}
	}
	th,
	td {
		margin: 0.5em 1em;
		text-align: left;
		border-top: 1px solid $primary-color;
		@include mobile-lg {
			border: none;
			margin: 0 1em;
		}
	}
	th {
		background: $secondary-color;
		color: $primary-color;
		font-weight: 500;
		@include mobile-lg {
			display: none;
		}
	}
	td {
		background: none;
		color: $white;
		text-align: center;
		@include mobile-lg {
			display: block;
			text-align: left;
		}
		&:before {
			content: attr(data-th) " : ";
			display: none;
			@include mobile-lg {
				display: inline-block;
				color: $primary-color;
			}
		}
	}
}
.footable {
	background: $white;
	/*tr[data-expanded="true"]{
		background: $secondary-color;
		color: $white;
		.fooicon-minus:before{
			background: url(../images/ic-minus-light.svg) no-repeat;
			background-size: contain;
		}
	}*/
	.footable-detail-row {
		background: $white;
	}
	.footable-details {
		background: $secondary-pale-color;
		tbody {
			border-bottom: 2px solid $secondary-color;
			tr {
				border-bottom: 1px solid $secondary-light-color;
			}
			th {
				background: $secondary-color;
				color: $white;
				text-align: right;
				vertical-align: middle;
			}
			td {
				text-align: left;
				height: 40px;
			}
		}
	}
	.footable-filtering {
		position: relative;
		z-index: 100;
		th {
			background: $pale-color;
		}
		&-search {
			display: flex;
			justify-content: flex-end;
			.input-group {
				max-width: 250px;
				input {
					border-radius: 50rem;
					padding-left: 20px;
					border-right: none;
				}
				.input-group-btn {
					button:first-child {
						border-radius: 0 50rem 50rem 0;
						background: $white;
						border-left: 0;
						border: 1px solid $primary-light-color;
						border-left: none;
						background: url("../images/ic-search-blue.svg") no-repeat center $white;
						background-size: 20px;
						width: 37px;
						height: 37px;
						margin-right: 8px;
						span {
							display: none;
						}
					}
					button:nth-child(2) {
						width: 37px;
						height: 37px;
						border-radius: 50%;
						background: url("../images/ic-filter.svg") no-repeat center $primary-color;
						&::after {
							display: none;
						}
					}
				}
			}
		}
	}
}
.info-table-wrapper{
	margin-top: -60px;
	@include mobile-lg{
		margin-top: -15px;
	}
}
.input-group-btn.open {
	position: relative;
	z-index: 1000;
	button:nth-child(2) {
		background: url("../images/ic-filter.svg") no-repeat center $secondary-color !important;
	}
	.dropdown-menu {
		display: block;
		position: absolute;
		right: 0;
		top: 40px;
		box-shadow: 0 0 5px $gray-light;
		padding: 16px;
		display: flex;
		flex-wrap: wrap;
		width: 350px;
		border-radius: 8px;
		@include mobile-lg{
			width: 300px;
		}
		@include mobile-md{
			width: 280px;
		}
		li {
			width: 50%;
			&:last-child{
				display: none;
			}
		}
		label {
			color: $gray-dark;
		}
		input[type="checkbox"] {
			width: 14px;
			height: 14px;
			border: 1px solid $white !important;
			margin-top: 4px;
			position: relative;
		&:before{
			content: "";
			width: 16px;
			height: 16px;
			border: 1px solid $gray !important;
			background: $white;
			border-radius: 3px;
			overflow: hidden;
			margin-right: 16px;
			overflow: hidden;
			position: absolute;
				left: -1px;
				top: 0;	
		}
		}
		input[type="checkbox"]:checked {
			border-radius: 3px;
			background: none;
			border: none;
			&:after {
				content: "";
				width: 16px;
				height: 16px;
				background: url("../images/ic-check-light.svg") no-repeat center $secondary-color;
				background-size: 10px;
				position: absolute;
				left: -1px;
				top: 0;
				border-radius: 3px;
			}
			color: $secondary-color !important;
			background: $secondary-color !important;
		}
	}
}

.info-table-wrapper {
	height: calc(100% - 50px);
	overflow: auto;
	thead {
		position: sticky;
		top: -2px;
		z-index: 1;
	}
}
.filter-btn {
	&.collapsed {
		background: $primary-color !important;
		border: 1px solid $primary-color !important;
	}
}

.filter-box {
	position: absolute;
	right: 16px;
	top: 60px;
	z-index: 3;
	width: 345px;
	box-shadow: 0 0 8px $gray;
	border: 0;
	border-radius: 5px;
	background: $white;
	overflow: hidden;
	@include mobile-lg {
		width: 280px;
		top: 100px;
		right: 10px;
	}
	&:before {
		content: "";
		position: absolute;
		display: block;
		width: 22px;
		height: 13px;
		background: url("../images/ic-point.svg") no-repeat;
		right: 12px;
		top: -13px;
		@include mobile-lg {
			right: 5px;
		}
	}
}

.filter-box-item {
	width: 33.33%;
	@include mobile-lg {
		width: 50%;
	}
}
