/*------------------------登入頁設定開始----------------------------*/
.wrapper[data-bg="login"]{
  min-height: 100vh;
  background: 
  url("../images/bg-login-top.svg") no-repeat left top,
  url("../images/bg-login-right.png") no-repeat right bottom,
  url("../images/bg-login-left.svg") no-repeat left bottom;
  background-size: 850px,950px,667px;
  @include PC-lg{
    background-size: 800px,60%,600px;
  }
  @include PC-md{
    background-size: 700px,55%,550px;
  }
  @include PC{
    background-size: 650px,50%,500px;
  }
  @include mobile-lg{
    background-size: 200px,80%,250px;
  }
}

.login{
  display: flex;
  min-height: 100vh;
  align-items: center;
  padding-left: 100px;
  @include PC-sm{
    padding-top: 30%;
    padding-left: 0;
    align-items: flex-start;
  }
  @include mobile-md{
    padding-top: 20px;
    padding-bottom: 200px;
  }
  &-brand{
    width: 100%;
    max-width: 528px;
  }
  &-title{
    @include text-indent;
    width: 100%;
    height: 60px;
    background: url("../images/title.svg") no-repeat;
    background-size: 100%;
  }
  input{
    border-radius: 50rem;
    border: none;
    background: $primary-xlight-color;
    padding-left: 20px;
    height: 55px;
    @include PC-md{
      height: auto;
    }
    &::placeholder{
      color: $base-color!important;
      @include mobile-md{
        font-size: $font-xxs!important;
      }
    }
    &[data-con="account"]{
      background: url("../images/ic-account.svg") no-repeat 12px center $primary-xlight-color;
      padding-left: 55px;
    }
    &[data-con="password"]{
      background: url("../images/ic-password.svg") no-repeat 12px center $primary-xlight-color;
      padding-left: 55px;
    }
  }
  .btn{
    height: 55px;
    @include PC-md{
      height: auto;
    }
  }
}
