@charset "utf-8";
@import url("https://fonts.googleapis.com/earlyaccess/notosanstc.css");
@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@600&display=swap");
@import url("../webfonts/glyphicons-halflings-regular.ttf");

/* --------------基本設定開始--------------- */
*,
*::before,
*::after {
	box-sizing: border-box;
}

html,
body {
	width: 100%;
	-webkit-text-size-adjust: none;
}
body {
	-webkit-font-smoothing: antialiased;
	-webkit-overflow-scrolling: touch;
	font-family: "Noto Sans TC", "Microsoft JhengHei", "sans-serif";
	line-height: 1.5;
	letter-spacing: 1px;
	color: $gray-dark;
	font-size: 1.1em;//字級加大
}
/* --------------字級加大開始--------------- */
.btn,.form-select{
	font-size: $font-sm;
}
.login .btn{
	font-size: $font-md;
}
.modal-body-title{
	font-size: $font-smd;
}
.circle-btn{
	width: 36px !important;
	height: 36px !important;
	flex-basis:36px !important;
}
/* --------------字級加大結束--------------- */
.clear {
	clear: both;
	height: 0;
	font-size: 1px;
	line-height: 0px;
}
img {
	display: block;
	max-width: 100%;
	height: auto;
}
a,
a:hover,
button,
button:hover {
	transition: all 0.2s ease;
	text-decoration: none;
}
a:focus{
	outline: none;
}
p {
	margin-bottom: 0;
}
ul {
	padding-left: 0;
	list-style: none;
	margin-bottom: 0;
}
ol {
	margin-bottom: 0;
}

input::placeholder {
	color: lighten($color: $gray, $amount: 15%)!important;
	font-size: $font-xsm;
}
.form-check-input:checked{
	background: $secondary-color;
	border: 1px solid $secondary-color;
}
.form-select{
	background: url("../images/ic-down-arrow.svg") no-repeat right center $white;
	border: 1px solid $primary-light-color;
	color: $gray-dark;
}
.form-control{
	font-size: $font-sm;
	color: $gray-dark;
	border: 1px solid $primary-light-color;
	&[readonly] {
	background: lighten($color: $primary-xxlight-color, $amount: 3%);
	border: 1px solid $gray-xlight;
	box-shadow: none;
}
}
.input-group-text{
	background: $white;
	border: 1px solid $primary-light-color;
	border-radius: 0 3px 3px 0;
	height: 100%;
	padding: 0 8px;
}
.datetimepicker-input{
	padding-right: 0;
}
.modal-dialog{
	@include PC-sm{
		max-width: 800px;
	}
}
.container{
	@include PC-sm{
		max-width: 800px;
	}
}
.modal-content{
	overflow: hidden;
	position: relative;
	.btn-close{
		background: url("../images/ic-cross.svg") center;
		background-size: 24px;
		opacity: 1;
		position: absolute;
		right: 10px;
		top: 11px;
		&:hover{
			opacity: 0.7;
		}
	}
}
.modal-header{
	padding: 10px 24px;
	justify-content: space-between;
	@include mobile-lg{
		padding: 10px 12px;
		flex-direction:column-reverse;
		&[data-txt="less"]{
			flex-direction: row;
			.btn{
				flex: 0 0 90px;
			}
			.modal-title{
				width: auto;
			}
		}
	}
}
.modal-title{
	display: flex;
	align-items: flex-start;
	color: $secondary-color;
	@include mobile-lg{
		width: 100%;
		font-size: $font-sm;
	}
	img{
		width: 24px;
		margin-right: 8px;
	}
}
.modal-body{
	font-size: $font-xs;
	label{
		color: $tertiary-color;
	}
}
.modal-xl{
	max-width: 1500px;
}